:root {
  --main-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  box-sizing: border-box;
  background: #fff;
  flex-shrink: 0;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  margin: auto;
  padding: 0 50px;
  display: flex;
}

.nav-list {
  color: #000;
  text-transform: uppercase;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  list-style: none;
  display: flex;
}

.nav-link {
  color: #000;
  justify-content: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav-item:nth-child(n+2) {
  margin-left: 20px;
}

.nav-link:hover {
  color: var(--main-color);
}

.is-active:after {
  content: "";
  background: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav {
  margin: 0;
  padding: 0;
}

.is-active {
  color: var(--main-color);
}

.logo {
  width: 40px;
  height: 40px;
}

